.loginSectn{padding: 30px;margin: 0 auto;max-width: 557px;position: relative;top: -220px;background: #fff;box-shadow: 0px 30px 36px #557DA526;border-radius: 20px;z-index: 1}
.bannerSection{display: block;position: relative;height: 464px;}
.bannerSection::before{content: '';position: absolute;width: 100%;height:464px;background: transparent linear-gradient(248deg, #303F60 0%, #1A253C 100%) 0% 0% no-repeat padding-box;
left: 0;
}
.loginSectn form .login_btn{ display: inline-block;
    margin: 15px 0px;}
.signup_roles { display: inline-flex;
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 15px;}
    .signup_roles .role1{    border: 2px solid #ccc;
        border-radius: 5px;}
        .signup_roles .role2{    border: 2px solid #ccc; margin-left: 20px;
            border-radius: 5px;}
.signup_roles .notactive .btn_container { background-color: #fff; border: 1px solid #fff; color: #43AFFF; width: 148px; border-radius: 5px;}
.signup_roles .notactive .btn_container button{ background-color: #fff; border: 1px solid #fff; color: #43AFFF; width: 148px; border-radius: 5px;}
.forgotSectn form p{text-align: left;}


